import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  InputGroup,
  Modal,
} from "react-bootstrap";
import { IoMdArrowDropdown } from "react-icons/io";
import axios from "axios";
import styles from "./main.module.css";

const productData = [
  "Air Conditioner",
  "Refrigerator",
  "Washing Machine",
  "Dishwasher",
  "Styler",
  "Microwave Oven",
  "Water Purifiers",
  "Air purifier",
  "Television",
  "TONE (Earbuds)",
];

const modelData = [
  "Model 1",
  "Model 2",
  "Model 3",
  "Model 4",
  "Model 5",
  "Model 6",
  "Model 7",
  "Model 8",
  "Model 9",
  "Model 10",
];

function Main() {
  const [fIdShow, setFIdShow] = useState(false);
  const [modValidated, setModValidated] = useState(false);
  const [idValidated, setIdValidated] = useState(false);
  const [detailValidated, setDetailValidated] = useState(false);
  const [infoShow, setInfoShow] = useState({
    bool: false,
    message: "",
  });
  const [formData, setFormData] = useState({
    id: "",
    name: "",
    mobile: "",
    email: "",
    product: "",
    productDropDown: "",
    modelDropDown: "",
    dealer_name: "",
    i_date: "",
    i_num: "",
    mod_mobile: "",
    mod_product: "",
  });
  const [fileData, setFileData] = useState(null);

  const trimValues = (object) => {
    Object.keys(object).map(
      (k) =>
        (object[k] =
          typeof object[k] == "string"
            ? object[k].replace(/\s+/g, " ").trim()
            : object[k])
    );
    return object;
  };

  let handleProductBtn = () => {
    document.getElementById("product").classList.toggle("d-block");
  };

  let handleModelBtn = () => {
    if (productData.includes(formData.productDropDown)) {
      document.getElementById("model").classList.toggle("d-block");
    }
  };

  let handleProductClick = (e) => {
    setFormData({
      ...formData,
      productDropDown: e.target.innerHTML,
      modelDropDown: "",
    });
    handleProductBtn();
  };
  let handleModelClick = (e) => {
    setFormData({
      ...formData,
      modelDropDown: e.target.innerHTML,
    });
    handleModelBtn();
  };

  let handleInputChange = (event) => {
    const target = event.target;
    const name = target.name;
    let value = target.value;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  let handleFileChange = (e) => {
    setFileData(e.target.files[0]);
  };

  const handleIdSubmit = async (e) => {
    const form = e.currentTarget;
    e.preventDefault();
    if (form.checkValidity() === false) {
      e.stopPropagation();
      setIdValidated(true);
      setInfoShow({
        bool: true,
        message:
          "Error: Please enter the valid unique id sent to your mobile and press submit",
      });
    } else {
      try {
        await axios.get(`api/form/${formData.id}`).then(({ data }) =>
          setFormData({
            ...formData,
            id: data.id,
            name: data.name,
            email: data.email,
            mobile: data.mobile,
            product: data.product,
          })
        );
        setIdValidated(false);
      } catch (error) {
        setInfoShow({
          bool: true,
          message: `${error.response.data || error.message}`,
        });
      }
    }
  };

  let handleModProductBtn = () => {
    document.getElementById("mod_product").classList.toggle("d-block");
  };
  let handleModProductClick = (e) => {
    setFormData({
      ...formData,
      mod_product: e.target.innerHTML,
    });
    handleModProductBtn();
  };

  const handleModSubmit = async (e) => {
    const form = e.currentTarget;
    e.preventDefault();
    if (form.checkValidity() === false) {
      e.stopPropagation();
      setModValidated(true);
    } else if (!productData.includes(formData.mod_product)) {
      setFIdShow(false);
      setInfoShow({
        bool: true,
        message:
          "Error: Please select the correct product from the options or type exactly same as in option",
      });
    } else {
      try {
        await axios
          .post("api/form/forgetId", {
            mobile: formData.mod_mobile,
            product: formData.mod_product,
          })
          .then(({ data }) => {
            setFIdShow(false);
            setInfoShow({
              bool: true,
              message: `Your unique id: ${data.id}`,
            });
          });
        setFormData({
          mod_mobile: "",
          mod_product: "",
        });
        setModValidated(false);
      } catch (error) {
        setFIdShow(false);
        setInfoShow({
          bool: true,
          message: `${error.response.data || error.message}`,
        });
      }
    }
  };

  const forgetIdModal = () => (
    <Modal size="lg" show={fIdShow} onHide={() => setFIdShow(false)}>
      <Modal.Header closeButton>
        <h3 style={{ textAlign: "center" }}>
          Fill the given below details to get your unique registration ID
        </h3>
      </Modal.Header>
      <Form
        autoComplete="false"
        noValidate
        validated={modValidated}
        onSubmit={handleModSubmit}
      >
        <Modal.Body>
          <Row>
            <Col>
              <Form.Group>
                <Form.Label className={styles.label}>
                  Your Registred mobile number
                </Form.Label>
                <Form.Control
                  placeholder="*Mobile Number"
                  required
                  onChange={handleInputChange}
                  type="tel"
                  minLength="10"
                  maxLength="10"
                  pattern="[6789][0-9]{9}"
                  name="mod_mobile"
                  value={formData.mod_mobile}
                ></Form.Control>
                <Form.Control.Feedback type="invalid">
                  Please enter valid mobile number
                </Form.Control.Feedback>
              </Form.Group>
            </Col>
            <Col>
              <Form.Group>
                <Form.Label className={styles.label}>Product</Form.Label>
                <InputGroup>
                  <Form.Control
                    type="text"
                    placeholder="*Select Product"
                    required
                    name="mod_product"
                    value={formData.mod_product}
                    onChange={handleInputChange}
                  />

                  <Button
                    variant="outline-secondary"
                    className={styles.opt_btn}
                    onClick={handleModProductBtn}
                  >
                    <IoMdArrowDropdown />
                  </Button>
                </InputGroup>
                <span id="mod_product" className={styles.custom_dropdown}>
                  <span>
                    <ul>
                      {productData.map((data, i) => (
                        <li key={i} onClick={handleModProductClick}>
                          {data}
                        </li>
                      ))}
                    </ul>
                  </span>
                </span>
              </Form.Group>
            </Col>
          </Row>
        </Modal.Body>
        <Modal.Footer>
          <Button type="submit" variant="outline-success">
            Submit
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );

  const infoModal = (info) => {
    return (
      <Modal show={infoShow.bool} onHide={() => setInfoShow({ bool: false })}>
        <Modal.Header closeButton>{info}</Modal.Header>
      </Modal>
    );
  };

  let reset = () => {
    setFormData({
      id: "",
      name: "",
      mobile: "",
      email: "",
      product: "",
      productDropDown: "",
      modelDropDown: "",
      dealer_name: "",
      i_date: "",
      i_num: "",
      mod_mobile: "",
      mod_product: "",
    });
    setFileData("");
    document.querySelector("#i_upload").value = "";
  };

  const handleDetailSubmit = async (e) => {
    const id = document.getElementById("id_form");
    const form = e.currentTarget;
    e.preventDefault();
    if (id.checkValidity() === false || formData.name === "") {
      e.stopPropagation();
      setIdValidated(true);
      setInfoShow({
        bool: true,
        message:
          "Error: Please enter the valid unique id sent to your mobile and press submit",
      });
    } else {
      if (form.checkValidity() === false) {
        e.stopPropagation();
        setDetailValidated(true);
      } else if (
        !productData.includes(formData.productDropDown) ||
        !modelData.includes(formData.modelDropDown)
      ) {
        setFIdShow(false);
        setInfoShow({
          bool: true,
          message:
            "Error: Please select the correct product/model from the options or type exactly same as in option",
        });
      } else {
        try {
          trimValues(formData);
          const data = new FormData();

          for (const val in formData) {
            data.set(`${val}`, formData[val]);
            console.log(`${val}`, formData[val]);
          }

          data.append("i_upload", fileData);
          await axios.post(`api/form/${formData.id}`, data).then((resp) => {
            setInfoShow({
              bool: true,
              message: resp.data,
            });
            setIdValidated(false);
            setDetailValidated(false);
          });
        } catch (error) {
          setInfoShow({
            bool: true,
            message: `${error.response.data || error.message}`,
          });
        }
        reset();
      }
    }
  };

  return (
    <div>
      {forgetIdModal()}
      {infoModal(infoShow.message)}
      <div className={styles.header}>
        <h2>BUY FROM HOME</h2>
      </div>
      <div>
        <Container style={{ marginBottom: "5%" }}>
          <Row className={styles.row_1}>
            <Col>
              <Form
                autoComplete="false"
                noValidate
                onSubmit={handleIdSubmit}
                validated={idValidated}
                id="id_form"
              >
                <Form.Group>
                  <Form.Label className={styles.label}>
                    Enter the Unique Code.
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="XXXXXXXXXXXXXXXXXXXXXXXX"
                    required
                    maxLength="24"
                    minLength="24"
                    name="id"
                    value={formData.id}
                    onChange={handleInputChange}
                  ></Form.Control>
                  <Form.Control.Feedback type="invalid">
                    Please enter valid id
                  </Form.Control.Feedback>
                </Form.Group>
                <Row>
                  <Col md={6} lg={3}>
                    <Button
                      variant="outline-danger"
                      type="submit"
                      className={styles.btn}
                      style={{ width: "100%" }}
                    >
                      SUBMIT
                    </Button>
                  </Col>
                  <Col md={6} lg={3}>
                    <Button
                      variant="outline-danger"
                      className={styles.btn}
                      style={{ width: "100%" }}
                      onClick={() => setFIdShow(true)}
                    >
                      FORGET UNIQUE CODE
                    </Button>
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
          <Row className={styles.row_2}>
            <Col>
              <p>Post entry of unique code, data will reflect automatically.</p>
            </Col>
          </Row>
          <Form
            autoComplete="false"
            noValidate
            onSubmit={handleDetailSubmit}
            validated={detailValidated}
          >
            <Row>
              <Col md={6} lg={3}>
                <Form.Group>
                  <Form.Label className={styles.label}>
                    Customer Name
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="*Customer Name"
                    name="name"
                    value={formData.name}
                    readOnly
                    required
                  ></Form.Control>
                </Form.Group>
              </Col>
              <Col md={6} lg={3}>
                <Form.Group>
                  <Form.Label className={styles.label}>
                    Mobile Number
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="*Mobile Number"
                    name="mobile"
                    value={formData.mobile}
                    readOnly
                    required
                  ></Form.Control>
                </Form.Group>
              </Col>
              <Col md={6} lg={3}>
                <Form.Group>
                  <Form.Label className={styles.label}>Email ID</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Email ID"
                    name="email"
                    value={formData.email}
                    readOnly
                    required
                  ></Form.Control>
                </Form.Group>
              </Col>
              <Col md={6} lg={3}>
                <Form.Group>
                  <Form.Label className={styles.label}>Product</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="*Product"
                    name="product"
                    value={formData.product}
                    readOnly
                    required
                  ></Form.Control>
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col>
                <h4>
                  Enter the Purchase Details for verification &amp; E-Voucher
                  Claim
                </h4>
              </Col>
            </Row>
            <Row>
              <Col md={6} lg={2}>
                <Form.Group>
                  <Form.Label className={styles.label}>Product</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="text"
                      placeholder="*Product"
                      required
                      name="productDropDown"
                      value={formData.productDropDown}
                      onChange={handleInputChange}
                    />

                    <Button
                      variant="outline-secondary"
                      className={styles.opt_btn}
                      onClick={handleProductBtn}
                    >
                      <IoMdArrowDropdown />
                    </Button>
                  </InputGroup>
                  <span id="product" className={styles.custom_dropdown}>
                    <span>
                      <ul>
                        {productData.map((data, i) => (
                          <li key={i} onClick={handleProductClick}>
                            {data}
                          </li>
                        ))}
                      </ul>
                    </span>
                  </span>
                </Form.Group>
              </Col>
              <Col md={6} lg={2}>
                <Form.Group>
                  <Form.Label className={styles.label}>Model Number</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="text"
                      placeholder="*Model Number"
                      required
                      name="modelDropDown"
                      value={formData.modelDropDown}
                      onChange={handleInputChange}
                    />

                    <Button
                      variant="outline-secondary"
                      className={styles.opt_btn}
                      onClick={handleModelBtn}
                    >
                      <IoMdArrowDropdown />
                    </Button>
                  </InputGroup>
                  <span id="model" className={styles.custom_dropdown}>
                    <span>
                      <ul>
                        {modelData.map((data, i) => (
                          <li key={i} onClick={handleModelClick}>
                            {data}
                          </li>
                        ))}
                      </ul>
                    </span>
                  </span>
                </Form.Group>
              </Col>
              <Col md={6} lg={2}>
                <Form.Group>
                  <Form.Label className={styles.label}>Dealer Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="*Dealer Name"
                    required
                    pattern="[A-Z a-z]{1,100}"
                    maxLength="100"
                    minLength="3"
                    name="dealer_name"
                    value={formData.dealer_name}
                    onChange={handleInputChange}
                  ></Form.Control>
                  <p className={styles.info}>(Type Minimum 3 char.)</p>
                  <Form.Control.Feedback type="invalid">
                    Please enter valid dealer name
                  </Form.Control.Feedback>
                </Form.Group>
              </Col>
              <Col md={6} lg={2}>
                <Form.Group>
                  <Form.Label className={styles.label}>Invoice Date</Form.Label>
                  <Form.Control
                    type="date"
                    placeholder="*Invoice Date"
                    required
                    name="i_date"
                    value={formData.i_date}
                    onChange={handleInputChange}
                  ></Form.Control>
                  <Form.Control.Feedback type="invalid">
                    Please enter valid invoice date
                  </Form.Control.Feedback>
                </Form.Group>
              </Col>
              <Col md={6} lg={2}>
                <Form.Group>
                  <Form.Label className={styles.label}>
                    Invoice Number
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="*Invoice Number"
                    required
                    minLength="16"
                    maxLength="16"
                    name="i_num"
                    value={formData.i_num}
                    onChange={handleInputChange}
                  ></Form.Control>
                  <Form.Control.Feedback type="invalid">
                    Please enter valid invoice number
                  </Form.Control.Feedback>
                </Form.Group>
              </Col>
              <Col md={6} lg={2}>
                <Form.Label className={styles.label}>Invoice Upload</Form.Label>

                <Form.Group>
                  <Form.File
                    className={styles.invoice}
                    onChange={(e) => handleFileChange(e)}
                    name="i_upload"
                    id="i_upload"
                  />
                  <p className={styles.info}>
                    (size 500KB and png/jpeg/jpg/pdf)
                  </p>
                </Form.Group>
              </Col>
            </Row>
            {/* <Row>
              <Col>
                <div style={{ textAlign: "right", fontSize: "1.3rem" }}>
                  + Click to add more products
                </div>
              </Col>
            </Row> */}
            <Row>
              <Col>
                <Button
                  variant="outline-danger"
                  type="submit"
                  className={styles.btn}
                >
                  SUBMIT YOUR DETAILS
                </Button>
              </Col>
            </Row>
          </Form>
        </Container>
      </div>
    </div>
  );
}

export default Main;
